body {
    font: 32px Arial, sans-serif;
    color: #F0F0F0;
    margin: 30px;
    background: #222222;
}
svg {
    height: 100%;
    width: 100%;
}
svg>circle {
    cx: 50%;
    cy: 50%;
    stroke-width: 1.5px;
}
h1 {
    text-align: center;
}
.game {
    display: flex;
    flex-direction: column;
    background: #404040;
    align-items: center;
}
.game-info {
    background: #333333;
}
/*.board-row:after {
    clear: both;
    content: "";
    display: table;
}*/
.square {
    background: #14A7D0;
    border: 2px solid #303030;
    float: left;
    height: 80px;
    margin-right: -1px;
    margin-top: -1px;
    padding: 0px;
    width: 80px;
    outline: none;
}
.square:hover {
    background: #24B7E0;
}
.square:active {
    background: #34C7F0;
}